/* Global Styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
}

.app-container {
  display: flex;
  height: 100vh;
}

/* Sidebar Styles */
.sidebar {
  width: 250px;
  background-color: #1e293b;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.sidebar-title {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
}

.sidebar-menu li {
  margin: 10px 0;
}

.sidebar-menu a {
  text-decoration: none;
  color: white;
  font-size: 0.6rem;
  padding: 10px;
  display: block;
  border-radius: 5px;
}

.sidebar-menu a:hover,
.sidebar-menu a.active {
  background-color: #334155;
}

/* Main Content */
.main-content {
  flex: 1;
  padding: 20px;
  background-color: white;
  overflow-y: auto;
}
